<template>
    <div class="container">
        <div>
            <label>Files
                <input type="file" id="file" ref="file" multiple v-on:change="handleFilesUpload()"/>
            </label>
            <button v-on:click="submitFiles()">Submit</button>
        </div>

        <transition name="fade">
          <p v-if="show">Ajout de l'image r√©ussi !</p>
        </transition>
  </div>
</template>

<script>
import { post } from "@/http-api.js"

export default {
    name: "UploadComponent",
    data() {
        return {
            file: '',
            show: false
        }
    },
    methods: {
      eraseMsg() {
        setTimeout(() => this.show = false, 2000);
      },
      /*
        Submits img to the server
      */
      submitFiles(){
        /*
          Initialize the form data
        */
        let formData = new FormData();

        formData.append('file', this.file, this.file.name);

        /*
          Make the request to the POST /images URL
        */
        post(formData,
          {
            headers: {
                'Content-Type': 'image/jpeg'
            }
          }, 
          () => {
            this.show = true;
            this.eraseMsg();
          },
          (err) => {
            this.show = false;
            console.log(err);
            this.eraseMsg();
          });
      },

      /*
        Handles a change on the file upload
      */
      handleFilesUpload(){
        this.file = this.$refs.file.files[0];
      }
    }
}
</script>

<style scoped>
.fade-leave-active {
  transition: opacity .5s ease;
}
.fade-leave-to {
  opacity: 0;
}
</style>